@mixin ab
	position: absolute

@mixin nomouse
	pointer-events: none
	user-select: none

@mixin re
	position: relative

@mixin fx
	position: fixed

@mixin allCenter
	display: flex
	align-items: center
	justify-content: center
	//flex-wrap: wrap

@mixin o($o)
	opacity: $o

@mixin fs($size)
	font-size: $size

@mixin borderR($radius)
	border-radius: $radius

@mixin ls($s)
	letter-spacing: $s

@mixin lh($h)
	line-height: $h

@mixin talign($align)	
	text-align: $align

@mixin ff($f)	
	font-family: $f

@mixin show
	display: block

@mixin hide
	display: none

@mixin bgColor($color)
	background-color: $color

@mixin full
	width: 100%
	height: 100%
	top: 0
	left: 0

@mixin minH($h)
	min-height: $h

@mixin h($h)
	height: $h

@mixin w($w)
	width: $w

@mixin t($t)
	top: $t

@mixin l($l)
	left: $l

@mixin r($r)
	right: $r

@mixin b($b)
	bottom: $b

@mixin mt($mt)
	margin-top: $mt

@mixin mb($mb)
	margin-bottom: $mb

@mixin ml($ml)
	margin-left: $ml

@mixin mr($mr)
	margin-right: $mr

@mixin pt($t)
	padding-top: $t

@mixin pb($b)
	padding-bottom: $b

@mixin pl($l)
	padding-left: $l

@mixin pr($r)
	padding-right: $r

@mixin valign($align)
	vertical-align: $align

@mixin setWH($w, $h)
	width: $w
	height: $h

@mixin setW($w)
	width: $w

@mixin setH($h)
	height: $h

@mixin hand
	cursor: pointer

@mixin setPosition($position, $p1, $p2)
	@if $position == "TL"
		top: $p1
		left: $p2
	@else if $position == "TC"
		left: 0
		right: 0
		top: $p1
		margin: auto
	@else if $position == "TR"
		top: $p1
		right: $p2
	@else if $position == "CL"
		left: $p2
		top: 0
		bottom: 0
		margin: auto
	@else if $position == "CC"
		left: 0
		right: 0
		top: 0
		bottom: 0
		margin: auto
	@else if $position == "CR"
		right: $p2
		top: 0
		bottom: 0
		margin: auto
	@else if $position == "BL"
		bottom: $p1
		left: $p2
	@else if $position == "BC"
		left: 0
		right: 0
		bottom: $p1
		margin: auto
	@else if $position == "BR"
		bottom: $p1
		right: $p2

@mixin setFont($size, $color, $ls)
	font-size: $size
	color: $color
	letter-spacing: $ls

@mixin setFont_NC($size, $ls)
	font-size: $size
	letter-spacing: $ls

@mixin opacityTo($from, $to)
	0%
		opacity: $from
	100%
		opacity: $to

@mixin respond-to($device)
	@if $device == portrait
		@media only screen and (orientation: portrait)
			@content
	@if $device == landscape
		@media only screen and (orientation: landscape)
			@content