@import reset
@import mixin

body
    +full
    overflow: hidden
    +bgColor(#000000)

canvas.a-canvas
    +ab
    +setWH(100%, 100%)
    +setPosition("TL",0,0)

div.a-enter-vr
    +hide

//line
img.line
    +ab
    +full
    +nomouse

//logo
img.logo
    +ab
    @include respond-to(landscape)
        +setW(7vw)
        +setPosition("TL", 7svh, 5vw)
    @include respond-to(portrait)
        +setW(30vw)
        +setPosition("TL", 6.3svh, 10vw)

//menu
//a.menu
img.menu
    +ab
    @include respond-to(landscape)
        +setW(2.4vw)
        +setPosition("TR", 6.3svh, 5vw)
    @include respond-to(portrait)
        +setW(10vw)
        +setPosition("TR", 5.4svh, 10vw)

//copyright
div.copyright
    +ab
    font-family: 'BebasRegular'
    @include respond-to(landscape)
        +setFont(1.5svh, #fff, 0.1vw)
        +setPosition("BR", 6svh, 7.2vw)
    @include respond-to(portrait)
        +setFont(1.9svh, #fff, 0.1vw)
        +setPosition("BR", 5.4svh, 20vw)

//music_control
div.music
    +ab
    @include respond-to(landscape)
        +setWH(1.5vw, 1.4svh)
        +setPosition("BR", 6svh, 5vw)
    @include respond-to(portrait)
        +setWH(6vw, 1.4svh)
        +setPosition("BR", 5.5svh, 10vw)
div.sound_bar
    +ab
    +bgColor(#fff)
    +o(0.18)
    @include respond-to(landscape)
        +setWH(100%, 0.2svh)
    @include respond-to(portrait)
        +setWH(100%, 0.2svh)
div.sound_bar.bar1
    @include respond-to(landscape)
        +setPosition("TR", 0, 0)
    @include respond-to(portrait)
        +setPosition("TR", 0, 0)
div.sound_bar.bar2
    @include respond-to(landscape)
        +setPosition("TR", 0.6svh, 0)
    @include respond-to(portrait)
        +setPosition("TR", 0.6svh, 0)
div.sound_bar.bar3
    @include respond-to(landscape)
        +setPosition("TR", 1.2svh, 0)
    @include respond-to(portrait)
        +setPosition("TR", 1.2svh, 0)
div.sound_line
    +ab
    +bgColor(#fff)
    @include respond-to(landscape)
        +setWH(0%, 0.2svh)
    @include respond-to(portrait)
        +setWH(0%, 0.2svh)
div.sound_line.line1
    @include respond-to(landscape)
        +setPosition("TR", 0, 0)
    @include respond-to(portrait)
        +setPosition("TR", 0, 0)
div.sound_line.line2
    @include respond-to(landscape)
        +setPosition("TR", 0.6svh, 0)
    @include respond-to(portrait)
        +setPosition("TR", 0.6svh, 0)
div.sound_line.line3
    @include respond-to(landscape)
        +setPosition("TR", 1.2svh, 0)
    @include respond-to(portrait)
        +setPosition("TR", 1.2svh, 0)

//qmimi_control
div.qmimi_control
    +ab
    +full
    +nomouse
div.qmimi_control_left
    +ab
    @include respond-to(landscape)
        +setWH(9.6vw, 9.6vw)
        +setPosition("BL", 5svh, 5vw)
    @include respond-to(portrait)
        +setWH(40vw, 40vw)
        +setPosition("BL", 5svh, 5vw)
div.qmimi_move_background
    +ab
    +full
    +bgColor(#F5B331)
    +o(0.1)
    @include respond-to(landscape)
        +borderR(3vw)
    @include respond-to(portrait)
        +borderR(12.5vw)
div.qmimi_move
    +ab
    @include respond-to(landscape)
        +setWH(6vw, 6vw)
        +setPosition("TL", 1.8vw, 1.8vw)
    @include respond-to(portrait)
        +setWH(25vw, 25vw)
        +setPosition("TL", 7.5vw, 7.5vw)
div.qmimi_move img
    +ab
    +full
img.move_pc
    @include respond-to(landscape)
        +show
    @include respond-to(portrait)
        +hide
img.move_mobile
    @include respond-to(landscape)
        +hide
    @include respond-to(portrait)
        +show

div.qmimi_consstrol_right
    +ab
    @include respond-to(landscape)
        +setWH(5vw, 2.83vw)
        +setPosition("BR", 10svh, 5vw)
    @include respond-to(portrait)
        +setWH(18vw, 10.2vw)
        +setPosition("BR", 10svh, 10vw)
img.jump_pc
    +ab
    +full
    @include respond-to(landscape)
        +show
    @include respond-to(portrait)
        +hide
img.jump_mobile
    +ab
    +full
    @include respond-to(landscape)
        +hide
    @include respond-to(portrait)
        +show

//car_in or car_out
div.car_btn
    +ab
    +hide
    @include respond-to(landscape)
        +setWH(5vw, 2.83vw)
        +setPosition("BL", 10svh, 47.5vw)
    @include respond-to(portrait)
        +setWH(18vw, 10.2vw)
        +setPosition("BL", 10svh, 50vw)
img.car_in
    +ab
    +full
    //+hide
img.car_out
    +ab
    +full
    //+hide

//car_control
div.car_control
    +ab
    +full
    +nomouse
    +hide
div.steering_wheel
    +ab
    @include respond-to(landscape)
        +setPosition("BL", 24svh, 10vw)
    @include respond-to(portrait)
        +setPosition("BL", 25svh, 28.6vw)
div.steering_wheel img
    +ab
    @include respond-to(landscape)
        +setWH(6vw,4.28vw)
        +setPosition("TL", -2.14vw, -3vw)
    @include respond-to(portrait)
        +setWH(24vw,17.14vw)
        +setPosition("TL", -8.57vw, -12vw)
div.left_right_controller
    +ab
    @include respond-to(landscape)
        +setWH(10vw, 2.83vw)
        +setPosition("BL", 10svh, 5vw)
    @include respond-to(portrait)
        +setWH(37vw, 10.2vw)
        +setPosition("BL", 10svh, 10vw)
div.left_right_bg
    +ab
    +full
    +bgColor(#F5B331)
    +o(0.12)
    @include respond-to(landscape)
        +borderR(0.5vw)
    @include respond-to(portrait)
        +borderR(1.8vw)
img.left_right_btn
    +ab
    @include respond-to(landscape)
        +setH(2.83vw)
    @include respond-to(portrait)
        +setH(10.2vw)
div.front_back_controller
    +ab
    @include respond-to(landscape)
        +setWH(2.83vw, 8vw)
        +setPosition("BR", 10svh, 5vw)
    @include respond-to(portrait)
        +setWH(10.2vw, 25vw)
        +setPosition("BR", 18svh, 10vw)
div.front_back_bg
    +ab
    +full
    +bgColor(#F5B331)
    +o(0.12)
    @include respond-to(landscape)
        +borderR(0.5vw)
    @include respond-to(portrait)
        +borderR(1.8vw)
img.front_back_btn
    +ab
    @include respond-to(landscape)
        +setW(2.83vw)
    @include respond-to(portrait)
        +setW(10.2vw)

div.stop_btn
    +ab
    @include respond-to(landscape)
        +setWH(5vw, 2.83vw)
        +setPosition("BR", 10svh, 10vw)
    @include respond-to(portrait)
        +setWH(18vw, 10.2vw)
        +setPosition("BR", 10svh, 10vw)
img.car_space
    +ab
    +full
    @include respond-to(landscape)
        +show
    @include respond-to(portrait)
        +hide
img.car_stop
    +ab
    +full
    @include respond-to(landscape)
        +hide
    @include respond-to(portrait)
        +show

//nav
div.nav
    +ab
    +full
    +bgColor(#000)
    //+o(0.5)
    +allCenter
    text-align: center
    flex-direction: column
    //+hide
img.nav_close
    +ab
    @include respond-to(landscape)
        +setW(2.4vw)
        +setPosition("TR", 6svh, 5vw)
    @include respond-to(portrait)
        +setW(8vw)
        +setPosition("TR", 6svh, 10vw)
div.nav a.nav_btn
    +re
    //+setWH(60vw,auto)
    max-width: 60vw
    display: inline-block
    text-decoration: none
    @include respond-to(landscape)
        +setFont_NC(5svh, 0.2svh)
    @include respond-to(portrait)
        +setFont_NC(5svh, 0.2svh)
a.nav_btn.work
    margin-bottom: 0.3svh
a.nav_btn.home
    margin-bottom: 2.4svh
a.nav_btn.work.about_me
    margin-top: 2.4svh
    margin-bottom: 6svh

a.nav_btn:hover
    color: #F5B331
a.email:hover
    color: #F5B331
a:link
    color: #ffffff
a:visited
    color: #ffffff
a:active
    color: #ffffff

.on
    color: #F5B331
.off
    color: #ffffff
.on_bg
    +bgColor(#F5B331)
.off_bg
    +bgColor(#ffffff)
.on_lable
    color: #F5B331
.off_lable
    color: #FFFFFF

//lable
div.lable_data
    +ab
    +setWH(40vw, 40s)
    display: flex
    justify-content: center
    flex-direction: column
    +nomouse
    @include respond-to(landscape)
        +setPosition("TL", 30svh, 5vw)
    @include respond-to(portrait)
        +setPosition("TL", 30svh, 10vw)
div.lable
    +re
    +setWH(40vw, auto)
    +minH(1vw)
span.box
    +re
    display: inline-block
    //+bgColor(#F5B331)
    @include respond-to(landscape)
        margin-right: 0.3vw
        +setWH(0.2vw, 0.2vw)
    @include respond-to(portrait)
        margin-right: 1vw
        +setWH(0.6vw, 0.6vw)
span.lable_name
    +re
    +setFont_NC(3svh, 0.2svh)
    +setWH(auto, auto)

div.email
    +re
    +setWH(60vw,auto)
    +allCenter
div.email img
    +re
    
    +setH(4svh)
    @include respond-to(landscape)
        margin-right: 1vw
    @include respond-to(portrait)
        margin-right: 2vw
div.email a.email
    +re
    text-decoration: none
    +setFont_NC(2svh, 0.2svh)

//work_pages
div.work_pages
    //+hide
    +ab
    //+bgColor(#000)
    overflow: hidden
    @include respond-to(landscape)
        +setWH(50vw,100svh)
        +setPosition("TL",0svh,50vw)
    @include respond-to(portrait)
        +setWH(100vw,50svh)
        +setPosition("BL",0svh,0vw)
div.work_page
    +ab
    +o(0)
    @include respond-to(landscape)
        +setWH(30vw, 70svh)
        +setPosition("TL",15svh,10vw)
    @include respond-to(portrait)
        +setWH(80vw, 36svh)
        +setPosition("TL",5svh,10vw)
p.work_title
    +re
    @include respond-to(landscape)
        +setW(30vw)
        +setFont(7svh, #fff, 0.2svh)
        margin-bottom: 1svh
    @include respond-to(portrait)
        +setW(80vw)
        +setFont(5svh, #fff, 0.2svh)
        margin-bottom: 1svh
p.work_data
    +re
    @include respond-to(landscape)
        +setW(30vw)
        +setFont(2.6svh, #fff, 0.2svh)
        font-family: BebasRegular 
    @include respond-to(portrait)
        +setW(80vw)
        +setFont(2.4svh, #fff, 0.2svh)
        font-family: BebasRegular 
a.arrow_right
    +ab
    @include respond-to(landscape)
        +setW(2.4vw)
        +setPosition("TR", 6svh, 5vw)
    @include respond-to(portrait)
        +setW(8vw)
        +setPosition("TR", 4svh, 6vw)
img.arrow_right
    +re
    +full

//mask
img.mask
    +ab
    +full
    +o(0.48)
    +nomouse

// 字型
@font-face 
    font-family: 'BebasRegular'
    font-weight: normal
    src: url('../fonts/BebasRegular.woff') format("woff")

@font-face 
    font-family: 'BebasBold'
    font-weight: normal
    src: url('../fonts/BebasBold.woff') format("woff")

@font-face 
    font-family: 'NotoSansTC'
    font-weight: normal
    src: url('https://fonts.gstatic.com/ea/notosanstc/v1/NotoSansTC-Regular.woff') format('woff')